import React from 'react';

class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      search: ''
    }
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(e) {
    this.setState({search:e.target.value});
  }
  setIndex(value) {
    let articles = this.props.articles['articles'];
    var index = articles.indexOf(value);
    console.log(index);
    this.props.index(index);
  }
  close() {
    this.props.close(false);
  }
  count(article) {
    var counter = 0;
    if (Object.keys(this.props.map).includes(article)) {
      counter = this.props.map[article].length;
    }
    return counter;
  }
  searcher() {
    let articles = this.props.articles['articles'];
    let query = this.state.search;
    var matches = [];
    for (var a in articles) {
      if (articles[a].toUpperCase().includes(query.toUpperCase())) {
        matches.push(articles[a]);
      }
    }
    return (
      <ul>
        {matches.map((match,index) => (
          <li key={index} onClick={() => this.setIndex(match)}><span class='map-count'>{this.count(match)}</span>{match}</li>
        ))}
      </ul>
    )
  }
  render() {
    return (
      <div id='map'>
        <div>
          <input type='text' value={this.state.search} onChange={this.handleChange}></input>
          {this.searcher()}
          <i className='fas fa-times' id='close-map' onClick={this.props.close}></i>
        </div>
      </div>
    )
  }
}

export default Search;
