import React from 'react';
import Articles from './data/articles.json';
import Categories from './data/categories.json';
import Search from './components/search.js';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      index: 0,
      map: {},
      searching: false
    }
    this.index = this.index.bind(this);
  }
  componentDidMount() {
    var data = localStorage.getItem('kb-category-map');
    var prevIndex = localStorage.getItem('kb-category-index');
    if (data !== null && prevIndex !== null) {
      var page = JSON.parse(prevIndex)['index'];
      this.setState({map: JSON.parse(data), index: page});
    }
  }
  download() {
    var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.map));
    var downloadAnchorNode = document.createElement('a');
    downloadAnchorNode.setAttribute("href",     dataStr);
    downloadAnchorNode.setAttribute("download", 'categorized.json');
    document.body.appendChild(downloadAnchorNode); // required for firefox
    downloadAnchorNode.click();
    downloadAnchorNode.remove();
  }
  save() {
    var i = {'index': this.state.index}
    localStorage.setItem('kb-category-map', JSON.stringify(this.state.map));
    localStorage.setItem('kb-category-index', JSON.stringify(i));
    alert('Saved! You can close this tab and pick up where you left off at any time.');
  }
  classify(target, value) {
    if (target === 'previous' && this.state.index === 0) {
      return 'shade'
    }
    if (target === 'checkbox' && this.isChecked(value)) {
      return 'checked'
    }
  }
  getUrl() {
    var url = Articles['articles'];
    return url[this.state.index];
  }
  isChecked(cat) {
    let map = this.state.map;
    let source = Articles['articles'];
    var checked = false;
    if (Object.keys(map).includes(source[this.state.index]) && Object.values(map[source[this.state.index]]).includes(cat)) {
      checked = true;
    }
    return checked;
  }
  check(cat) {
    let map = this.state.map;
    let source = Articles['articles'];
    if (!Object.keys(map).includes(source[this.state.index])) {
      map[source[this.state.index]] = []
      map[source[this.state.index]].push(cat)
    } else if (Object.keys(map).includes(source[this.state.index]) && !Object.values(map[source[this.state.index]]).includes(cat)) {
      map[source[this.state.index]].push(cat)
    } else if (Object.keys(map).includes(source[this.state.index]) && Object.values(map[source[this.state.index]]).includes(cat)) {
      var index = map[source[this.state.index]].indexOf(cat);
      map[source[this.state.index]].splice(index, 1);
    }
    this.setState({map: map});
  }
  nav(list) {
    return (
      <tbody>
        {list.map((category,index) => (
          <tr key={index}>
            <td className={this.classify('checkbox', category)} onClick={() => this.check(category)}><input type='checkbox' checked={this.isChecked(category)}></input>{category}</td>
          </tr>
        ))}
      </tbody>
    )
  }
  next() {
    if (Articles['articles'].length - this.state.index !== 0) {
      this.setState({index: this.state.index + 1});
    }
  }
  prev() {
    if (this.state.index !== 0) {
      this.setState({index: this.state.index - 1});
    }
  }
  triggerSearch(status) {
    this.setState({searching: status});
  }
  search() {
    if (this.state.searching) {
      return (
        <Search articles={Articles} map={this.state.map} close={() => this.triggerSearch(false)} index={this.index}/>
      )
    }
  }
  index(val) {
    this.setState({index:val, searching:false});
  }
  render() {
    const levels = ['Category 1', 'Category 2','Category 3'];
    return (
      <div id='main'>
        <iframe id='preview' title='preview' src={'https://www.papercut.com/kb/' + this.getUrl()}></iframe>
        <nav>
          <div id='badges'>
            <h4 onClick={() => this.triggerSearch(true)}>{Articles['articles'].length - this.state.index}</h4>
            <h4 onClick={() => this.save()}><i className='far fa-save'></i></h4>
            <h4 onClick={() => this.download()}><i className='fas fa-download'></i></h4>
          </div>
          <div id='categories'>
            {levels.map((level,index) => (
              <table key={index}>
                <caption>{level}</caption>
               {this.nav(Categories[index.toString()])}
              </table>
            ))}
          </div>
          <div id='crawler'>
            <div>
              <button className={this.classify('previous')} id='previous' onClick={() => this.prev()}>Previous</button>
              <button id='next' onClick={() => this.next()}>Next</button>
            </div>
          </div>
        </nav>
        {this.search()}
      </div>
    );
  }
}

export default App;
